<template>
  <div class="routingRole">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button  size="small" @click="routingAdd()" type="warning" icon="el-icon-plus">{{$t('common.add')}}</el-button>
      <div class="inline-block ml-10" v-if="tableVal.length !== 0">
        <el-button  type="danger" size="small" @click="routingDelete()">{{$t('common.delete')}}</el-button>
        <el-button  type="primary" size="small" @click="startEffect()">{{$t('common.start')}}</el-button>
        <el-button  type="primary" size="small" @click="stopEffect()">{{$t('common.stop')}}</el-button>
      </div>
    </div>
    <el-form :model="serviceForm" ref="serviceForm" label-width="94px" label-position="right" size="small"  :rules="inputRules">
      <el-row class="label-first" :gutter="20">
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('redis.redisCluster.clusterName')+'：'" prop="zkClusterId">
            <el-select v-model="serviceForm.zkClusterId" clearable :placeholder="$t('common.pleaseChoose')" style="width:100%">
              <el-option
                v-for="(item) in clusterOption"
                :key="item.id"
                :label="item.pkDesc"
                :value="item.pkValue">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
            <el-form-item :label="$t('service.serviceList.serviceName')+'：'" label-width="68px">
              <el-input type="text" v-model.trim="serviceForm.serviceName" clearable></el-input>
            </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-button size="small" type="primary" icon="el-icon-search" @click="search()">{{$t('common.search')}}</el-button>
        </el-col>
      </el-row>
    </el-form>
    <!-- 表格部分 -->
    <el-table
      stripe
      :data="routintList"
      v-loading="loading"
      ref="multipleTable"
      @selection-change="chooseBox"
      style="width: 100%">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            align="center"
            prop="serviceName"
            :label="$t('service.serviceList.serviceName')">
          </el-table-column>
          <el-table-column
            align="center"
            prop="routerName"
            :label="$t('service.routingRole.routingName')"
            width="110">
          </el-table-column>
          <el-table-column
            align="center"
            prop="priority"
            :label="$t('service.routingRole.priority')"
            width="110">
          </el-table-column>
          <el-table-column
            align="center"
            prop="method"
            :label="$t('service.routingRole.methodName')"
            width="110">
          </el-table-column>
          <el-table-column
            align="center"
            prop="routerStatus"
            :label="$t('service.provider.status')"
            width="110">
            <template slot-scope="props">
              <el-tag v-if="props.row.routerStatus === 'true'" size="mini">已启用</el-tag>
              <el-tag type="danger" v-else size="mini">已停止</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            width="120"
            :label="$t('common.operation')"
            fixed="right"
            align="center">
            <template slot-scope="props">
              <el-button type="text" size="mini" icon="fa fa-pencil-square-o" @click="routingDelete(props)">{{$t('common.delete')}}</el-button>
              <el-button v-if="props.row.routerStatus === 'false'" type="text" size="mini" icon="fa fa-play" @click="startEffect(props)" style="color:	#67c23a">{{$t('common.start')}}</el-button>
              <el-button v-if="props.row.routerStatus === 'true'" type="text" size="mini" icon="fa fa-stop" @click="stopEffect(props)" style="color:	#FF0000">{{$t('common.stop')}}</el-button>
            </template>
          </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="10"
        layout="total, prev, pager, next, jumper"
        background
        :total="totalPage">
      </el-pagination>
    </div>
    <!-- 弹出框 -->
    <el-dialog :title="'添加路由配置'" @close="cancel()" :visible.sync="popBox" width="45%" center>
      <el-form ref="popForm" :model="popForm" label-width="110px" size="small" status-icon :rules="inputRules">
        <!-- 第一：环境和集群 -->
        <el-row>
          <el-col :span="12">
            <el-form-item :label="$t('redis.redisCluster.clusterName')+'：'" prop="zkClusterId">
              <el-select v-model="popForm.zkClusterId" clearable :placeholder="$t('common.pleaseChoose')" style="width:100%">
                <el-option
                  v-for="(item) in clusterOption"
                  :key="item.id"
                  :label="item.pkDesc"
                  :value="item.pkValue">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item :label="$t('service.serviceList.serviceName')+'：'" prop="serviceName">
              <el-input type="text" v-model.trim="popForm.serviceName" placeholder="请输入1-100位字符" maxlength="100" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <!-- 第二：路由和优先级 -->
        <el-row>
          <el-col :span="12">
            <el-form-item :label="$t('service.routingRole.routingName')+'：'" prop="routerName">
              <el-input type="text" v-model.trim="popForm.routerName" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item :label="$t('service.routingRole.priority')+'：'" prop="priority">
              <el-select v-model="popForm.priority" clearable :placeholder="$t('common.pleaseChoose')">
                <el-option
                  v-for="item in classify.priorityGroup"
                  :key="item.id"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item :label="$t('service.routingRole.methodName')+'：'">
              <el-input type="text" v-model.trim="popForm.method" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <!-- 匹配条件 -->
          <el-row style="margin-bottom:16px">
            <el-col :span="5" :offset="7">
              <el-tag class="align" type="success">{{$t('service.routingRole.mate')}}</el-tag>
            </el-col>
            <el-col :span="5" :offset="7"><el-tag type="success">{{$t('service.routingRole.misMate')}}</el-tag></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item :label="$t('service.routingRole.consumerHost')+'：'">
                <el-input type="text" v-model.trim="popForm.consumerHost" placeholder="请输入1-25位字符" maxlength="25" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item>
                <el-input type="text" v-model.trim="popForm.unconsumerHost" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item :label="$t('service.routingRole.consumerApplication')+'：'">
                <el-input type="text" v-model.trim="popForm.consumerApplication" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item>
                <el-input type="text" v-model.trim="popForm.unconsumerApplication" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item :label="$t('service.routingRole.consumerCluster')+'：'">
                <el-input type="text" v-model.trim="popForm.consumerCluster" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item>
                <el-input type="text" v-model.trim="popForm.unconsumerCluster" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row style="margin-bottom:16px">
            <el-col :span="5" :offset="7">
              <el-tag class="align" type="success">{{$t('service.routingRole.mate')}}</el-tag>
            </el-col>
            <el-col :span="5" :offset="7"><el-tag type="success">{{$t('service.routingRole.misMate')}}</el-tag></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item :label="$t('service.routingRole.providerHost')+'：'">
                <el-input type="text" v-model.trim="popForm.providerHost" placeholder="请输入1-25位字符" maxlength="25" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item>
                <el-input type="text" v-model.trim="popForm.unproviderHost" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item :label="$t('service.routingRole.providerCluster')+'：'">
                <el-input type="text" v-model.trim="popForm.providerCluster" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item>
                <el-input type="text" v-model.trim="popForm.unproviderCluster" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item :label="$t('service.routingRole.providerProtocol')+'：'">
                <el-input type="text" v-model.trim="popForm.providerProtocol" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item>
                <el-input type="text" v-model.trim="popForm.unproviderProtocol" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item :label="$t('service.routingRole.providerPort')+'：'">
                <el-input type="text" v-model.trim="popForm.providerPort" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item>
                <el-input type="text" v-model.trim="popForm.unproviderPort" placeholder="请输入1-50位字符" maxlength="50" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :loading="loading" @click="popOk()" type="primary" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="cancel()" size="small">{{$t('common.cancel')}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import Api from '@/constants/Api.js'
  export default {
    name: 'routingRole',
    data() {
      return {
        currentPage: 1, // 当前页数
        totalPage: 0, // 总页数
        loading: false,
        // 查询/弹出框-规则
        inputRules: {
          zkClusterId: [
            { required: true, message: '请输入集群名称', trigger: 'blur' }
          ],
          serviceName: [
            { required: true, message: '请输入服务名', trigger: 'blur' }
          ],
          routerName: [
            { required: true, message: '请输入路由名称', trigger: 'blur' }
          ],
          priority: [
            { required: true, message: '请选择优先级', trigger: 'blur' }
          ],
          method: [
            { required: true, message: '请输入方法名', trigger: 'blur' }
          ]
        },
        // 查询模板
        serviceForm: {},
        // 全部数据
        dataBox: [],
        // table数据
        routintList: [],
        // 获得表格多选按钮
        tableVal: [],
        // 弹出框
        popBox: false,
        // 弹出框模板
        popForm: {}
      }
    },
    computed: {
      ...mapState({
        classify: state => state.service.classify,
        clusterOption: state => state.service.clusterOption
      })
    },
    activated() {
      const vm = this
      if (this.clusterOption.length <= 0) {
        this.initializeData({vm})
      }
      let {zkClusterId} = vm.$route.params
      if (zkClusterId) {
        vm.serviceForm = Object.assign({}, {zkClusterId, 'serviceName': vm.$route.params.name})
        vm.search()
      }
    },
    methods: {
      ...mapActions('service', ['initializeData']),
      // 拿到选择的内容
      chooseBox(val) {
        this.tableVal = val
      },
      // 翻页事件
      handleCurrentChange(val) {
        this.currentPage = val
        this.routintList = this.dataBox[val - 1]
      },
      // 查询
      search() {
        let {zkClusterId} = this.serviceForm
        const query = Object.assign({}, this.serviceForm)
        if (!zkClusterId) {
          this.$message({
            showClose: true,
            message: '带* 值不能为空',
            type: 'warning'
          })
          return false
        }
        this.loading = true
        this.$axios.post(Api.serviceFindRouter, {...query}).then((res) => {
          res.data.forEach((value) => {
            if (!value.zkClusterId) {
              value.zkClusterIdBackup = this.serviceForm.zkClusterId
            }
          })
          let data = JSON.parse(JSON.stringify(res.data))
          this.totalPage = data.length
          this.dataBox = this.conmmonMethods.arrayHandle(data)
          this.routintList = this.dataBox[this.currentPage - 1]
          this.loading = false
        }).catch((error) => {
          this.loading = false
          this.totalPage = 0
          this.dataBox = []
          this.currentPage = 1
          this.routintList = []
          this.$message.error(error.data.message || error.message || error)
        })
      },
      // 新增按钮
      routingAdd() {
        this.popForm = {}
        this.popBox = true
      },
      // 新增确定
      popOk() {
        let {zkClusterId, routerName, priority, serviceName} = this.popForm
        if (!zkClusterId || !routerName || !priority || !serviceName) {
          this.$message({
            showClose: true,
            message: '带*不能为空',
            type: 'warning'
          })
          return
        }
        const query = Object.assign({}, this.popForm)
        query.enable = true
        this.loading = true
        this.$axios.post(Api.serviceAddRoute, {
          ...query
        }).then((res) => {
          if (res.code === '0000') {
              this.$message({
                message: '新增成功',
                type: 'success'
              })
              this.serviceForm.zkClusterId = query.zkClusterId
              this.search()
            }
        }).catch((error) => {
          this.loading = false
          this.$message.error(error.data.message || error.message || error)
        })
        this.popBox = false
      },
      cancel() {
        this.$refs.popForm.resetFields()
        this.popBox = false
      },
      // 删除按钮
      routingDelete(props) {
        let query = []
        if (props) {
          let {zkClusterId, serviceName, routerName} = props.row
          if (!zkClusterId) {
            zkClusterId = props.row.zkClusterIdBackup
          }
          query.push({zkClusterId, serviceName, routerName})
        } else {
            if (Array.isArray(this.tableVal)) {
              this.tableVal.forEach((value, index) => {
                if (!value.zkClusterId) {
                  value.zkClusterId = value.zkClusterIdBackup
                }
                let {zkClusterId, serviceName, routerName} = value
                query.push({zkClusterId, serviceName, routerName})
              })
            }
        }
        query = JSON.stringify(query)
        this.conmmonMethods.makePopBox(this, '删除').then(() => {
          this.loading = true
          this.$axios.post(Api.serviceDeleRoute, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '删除成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      // 启动按钮
      startEffect(props) {
        let query = []
        if (props) {
          let {zkClusterId, serviceName, routerName} = props.row
          if (!zkClusterId) {
            zkClusterId = props.row.zkClusterIdBackup
          }
          query.push({zkClusterId, serviceName, routerName})
        } else {
          if (Array.isArray(this.tableVal)) {
            this.tableVal.forEach((value, index) => {
              if (!value.zkClusterId) {
                value.zkClusterId = value.zkClusterIdBackup
              }
              let {zkClusterId, serviceName, routerName} = value
              query.push({zkClusterId, serviceName, routerName})
            })
          }
        }
        query = JSON.stringify(query)
        this.conmmonMethods.makePopBox(this, '启动').then(() => {
          this.loading = true
          this.$axios.post(Api.serviceEnableRoute, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '启用成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      // 停用按钮
      stopEffect(props) {
        let query = []
        if (props) {
          let {zkClusterId, serviceName, routerName} = props.row
          if (!zkClusterId) {
            zkClusterId = props.row.zkClusterIdBackup
          }
          query.push({zkClusterId, serviceName, routerName})
        } else {
          if (Array.isArray(this.tableVal)) {
            this.tableVal.forEach((value, index) => {
              if (!value.zkClusterId) {
                value.zkClusterId = value.zkClusterIdBackup
              }
              let {zkClusterId, serviceName, routerName} = value
              query.push({zkClusterId, serviceName, routerName})
            })
          }
        }
        query = JSON.stringify(query)
        this.conmmonMethods.makePopBox(this, '停用').then(() => {
          this.loading = true
          this.$axios.post(Api.serviceDisableRoute, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '禁用成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      }
    }
  }
</script>

<style type="text/scss" lang="scss" scoped>
</style>



